'use client';

import { useState, useEffect, useCallback, useMemo } from 'react';
import { useRouter } from 'next/navigation';
import { generateTest } from '@/lib/questions';
import type { Question, Difficulty, TestType, TestSettings } from '@/types';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Progress } from '@/components/ui/progress';
import { Timer, AlertTriangle, Loader2 } from 'lucide-react';
import { cn } from '@/lib/utils';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"


export default function TestPageClient({ testId, difficulty, settings }: { testId: TestType; difficulty: Difficulty, settings: TestSettings }) {
  const router = useRouter();
  const [questions, setQuestions] = useState<Question[]>([]);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [userAnswers, setUserAnswers] = useState<(number | null)[]>([]);
  const [timeLeft, setTimeLeft] = useState(settings.timeLimit);
  const [selectedOption, setSelectedOption] = useState<number | null>(null);
  const [isAnswered, setIsAnswered] = useState(false);
  const [startTime, setStartTime] = useState<number | null>(null);

  useEffect(() => {
    const generatedQuestions = generateTest(testId, difficulty);
    setQuestions(generatedQuestions);
    setUserAnswers(new Array(generatedQuestions.length).fill(null));
    setStartTime(Date.now());
  }, [testId, difficulty]);

  const finishTest = useCallback(() => {
    const score = userAnswers.reduce((acc, answer, index) => {
        if (answer !== null && questions.length > 0 && answer === questions[index].answer) {
            return acc + 1;
        }
        return acc;
    }, 0);
    const endTime = Date.now();
    const timeTaken = startTime ? Math.floor((endTime - startTime) / 1000) : settings.timeLimit - timeLeft;
    
    router.replace(`/results?score=${score}&total=${questions.length}&time=${timeTaken}`);
  }, [userAnswers, questions, router, settings.timeLimit, timeLeft, startTime]);

  useEffect(() => {
    if (questions.length === 0) return;
    
    if (timeLeft <= 0) {
      finishTest();
      return;
    }

    const timer = setInterval(() => {
      setTimeLeft((prev) => prev - 1);
    }, 1000);

    return () => clearInterval(timer);
  }, [timeLeft, finishTest, questions.length]);

  const handleAnswer = (answer: number) => {
    if (isAnswered) return;
    setIsAnswered(true);
    setSelectedOption(answer);

    const newAnswers = [...userAnswers];
    newAnswers[currentQuestionIndex] = answer;
    setUserAnswers(newAnswers);

    setTimeout(() => {
      if (currentQuestionIndex < questions.length - 1) {
        setCurrentQuestionIndex(prev => prev + 1);
        setSelectedOption(null);
        setIsAnswered(false);
      } else {
        finishTest();
      }
    }, 700);
  };
  
  const currentQuestion = useMemo(() => questions[currentQuestionIndex], [questions, currentQuestionIndex]);
  const progress = (currentQuestionIndex / questions.length) * 100;
  const minutes = Math.floor(timeLeft / 60);
  const seconds = timeLeft % 60;
  
  if (questions.length === 0) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-4 text-lg">Generating your test...</p>
      </div>
    );
  }

  return (
    <div className="max-w-3xl mx-auto">
      <Card className="shadow-2xl relative overflow-hidden">
        <CardHeader>
          <div className="flex justify-between items-center mb-4">
            <CardTitle className="text-2xl font-headline">{settings.title}</CardTitle>
            <div className={cn("flex items-center gap-2 font-semibold text-lg p-2 rounded-md", timeLeft < 60 && 'text-destructive')}>
              <Timer className="h-6 w-6" />
              <span>{minutes}:{seconds.toString().padStart(2, '0')}</span>
            </div>
          </div>
          <CardDescription>Question {currentQuestionIndex + 1} of {questions.length}</CardDescription>
          <Progress value={progress} className="w-full mt-2" />
        </CardHeader>
        <CardContent>
          <div className="text-center my-8 md:my-12 transition-opacity duration-300" key={currentQuestionIndex}>
            <p className="text-5xl md:text-7xl font-bold tracking-wider text-primary-foreground">
              {currentQuestion.text} = ?
            </p>
          </div>
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
            {currentQuestion.options.map((option, index) => {
              const isCorrect = option === currentQuestion.answer;
              const isSelected = selectedOption === option;
              
              return (
                <Button
                  key={index}
                  onClick={() => handleAnswer(option)}
                  disabled={isAnswered}
                  className={cn(
                    "h-20 text-3xl font-bold transition-all duration-300 transform hover:scale-105",
                    isAnswered && isSelected && !isCorrect && "bg-destructive hover:bg-destructive/90 shake",
                    isAnswered && isCorrect && "bg-green-500 hover:bg-green-500/90",
                  )}
                  variant="outline"
                >
                  {option}
                </Button>
              )
            })}
          </div>
        </CardContent>
      </Card>
      <div className="mt-6 flex justify-end">
        <AlertDialog>
          <AlertDialogTrigger asChild>
            <Button variant="destructive">
                <AlertTriangle className="mr-2 h-4 w-4" />
                End Test
            </Button>
          </AlertDialogTrigger>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>Are you sure you want to end the test?</AlertDialogTitle>
              <AlertDialogDescription>
                Your progress will be saved and you will be taken to the results page. You cannot undo this action.
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter>
              <AlertDialogAction onClick={finishTest}>Yes, end test</AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
      </div>
       <style jsx>{`
        @keyframes shake {
          0%, 100% { transform: translateX(0); }
          10%, 30%, 50%, 70%, 90% { transform: translateX(-5px); }
          20%, 40%, 60%, 80% { transform: translateX(5px); }
        }
        .shake {
          animation: shake 0.5s ease-in-out;
        }
      `}</style>
    </div>
  );
}
